import { Injectable } from '@angular/core';
import { SurfacesService } from './surfaces.service';
import { Artifact } from '../models/artifact.model';

@Injectable({
  providedIn: 'root',
})
export class ArtifactsService {

  constructor(
    private surfacesService: SurfacesService,
  ) { }

  saveArtifact(projectId: number, environment: string, artifact: Artifact){
    let surface = this.surfacesService.getSurface(projectId, environment);
    if(surface != null){

      if(!surface.artifacts)
        surface.artifacts = [];

      surface.artifacts.push(artifact);
      this.surfacesService.updateSurface(projectId, surface);
    }
  }

  updateArtifacts(projectId: number, environment: string, updatedArtifact: Artifact){
    let surface = this.surfacesService.getSurface(projectId, environment);
    if(surface){
      const index = surface.artifacts
        .findIndex((artifact) => artifact.name == updatedArtifact.name);
      if(index != 1){
        surface.artifacts[index] = updatedArtifact;
        this.surfacesService.updateSurface(projectId, surface);
      }
    }
  }

  deleteArtifact(projectId: number, environment: string, name: string) {
    let surface = this.surfacesService.getSurface(projectId, environment);
    if(surface){
      surface.artifacts = surface.artifacts.filter(a => a.name != name);
      this.surfacesService.updateSurface(projectId, surface);
    }
  }

  getArtifact(projectId: number, environment: string, name: string): Artifact | null {
    let surface = this.surfacesService.getSurface(projectId, environment);
    return surface?.artifacts ? surface?.artifacts.find((artifact) => artifact.name == name) : null;
  }

  getArtifacts(projectId: number, environment: string): Artifact[] {
    let surface = this.surfacesService.getSurface(projectId, environment);
    return surface?.artifacts || [];
  }
}
