import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Circuit } from '../models/circuit.model';
import { CircuitsService } from '../services/circuits.service';
import { ActivatedRoute } from '@angular/router';
import { CircuitType } from '../models/circuittype.model';
import { ElectrificationDegreeType } from '../models/electrification-degree-type.model';
import { ProjectsCalculatorService, Protection, ProtectionType, SectionNominal, TechnicalFeature, TensionDrop } from '../services/projects-calculator.service';
import { SurfacesService } from '../services/surfaces.service';

@Component({
  selector: 'circuits',
  templateUrl: './circuits.component.html',
  styleUrls: ['./circuits.component.scss'],
})
export class CircuitsComponent implements OnInit {
  projectId: number;
  circuitForm: FormGroup;
  circuits: Circuit[];
  selectedCircuit: Circuit;
  isEdit: boolean;

  constructor(
    private circuitsService: CircuitsService,
    private surfacesService: SurfacesService,
    private fb: FormBuilder,
    private activatedRoute: ActivatedRoute,
    private projectsCalculatorService: ProjectsCalculatorService) { }

  ngOnInit(): void {
    this.circuitForm = this.fb.group({
      name: ['', Validators.required],
      circuitType: ['', Validators.required],
      numberOfMouths: ['', Validators.required],
      length: ['', Validators.required],
    });

    this.projectId = parseInt(this.activatedRoute.snapshot.params['projectId']);
    this.circuits = this.circuitsService.getCircuits(parseInt(this.activatedRoute.snapshot.params['projectId']));
    this.isEdit = false;
  }

  saveCircuit() {
    if(!this.circuitForm.invalid){
      if(!this.isEdit){
        let newCircuit = this.circuitForm.value as Circuit;
        this.circuitsService.saveCircuit(this.projectId, newCircuit);
      } else {
        let circuitToUpdate = this.circuitForm.value as Circuit;
        this.selectedCircuit.name = circuitToUpdate.name;
        this.selectedCircuit.circuitType = circuitToUpdate.circuitType;
        this.selectedCircuit.numberOfMouths = circuitToUpdate.numberOfMouths;
        this.selectedCircuit.length = circuitToUpdate.length;
        this.circuitsService.updateCircuit(this.projectId, this.selectedCircuit);
      }
      this.cleanForm();
    } else {
      alert('the form is invalid');
    }
  }

  cleanForm(): void {
    this.isEdit = false;
    this.circuitForm.controls['name'].setValue('');
    this.circuitForm.controls['circuitType'].setValue('');
    this.circuitForm.controls['numberOfMouths'].setValue('');
    this.circuitForm.controls['length'].setValue('');
    this.circuits = this.circuitsService.getCircuits(this.projectId);
  }

  editCircuit(name: string) {
    this.selectedCircuit = this.circuitsService.getCircuit(this.projectId, name);
    this.circuitForm.patchValue(this.selectedCircuit);
    this.isEdit = true;
  }

  deleteCircuit(name: string){
    this.circuitsService.deleteCircuit(this.projectId, name);
    this.cleanForm();
  }

  isFormInvalid() : boolean {
    return this.circuitForm.controls['name'].hasError('required')
    || this.circuitForm.controls['circuitType'].hasError('required')
    || this.circuitForm.controls['numberOfMouths'].hasError('required')
    || this.circuitForm.controls['length'].hasError('required');
  }

  circuitType(circuitType: CircuitType): CircuitType{
    return circuitType;
  }

  maxNumberOfMouths(circuitType: CircuitType): number {
    return this.projectsCalculatorService.maxNumberOfMouths(circuitType);
  }

  powerLoad(circuitType: CircuitType) : number {
    if(circuitType == CircuitType.LAP){
      return this.circuitLAPTotal();
    }
    return this.projectsCalculatorService.powerLoad(circuitType);
  }

  factorLoad(circuitType: CircuitType) : number {
    return this.projectsCalculatorService.factorLoad(circuitType);
  }

  simultaneityCoefficient(electrificationDegreeType: ElectrificationDegreeType){
    return this.projectsCalculatorService.simultaneityCoefficient(electrificationDegreeType);
  }

  electrificationDegree(electrificationDegreeType: ElectrificationDegreeType){
    return electrificationDegreeType;
  }

  circuitLAPTotal() : number {
    let surfaces = this.surfacesService.getSurfaces(this.projectId);
    let totalSurface = this.projectsCalculatorService.totalSurface(surfaces);
    let apparentPower = this.projectsCalculatorService.apparentPower(surfaces);
    let electrificationDegree = this.projectsCalculatorService.electrificationDegree(totalSurface, apparentPower);
    let simultaneityCoefficient = this.simultaneityCoefficient(electrificationDegree);
    return Math.ceil(this.projectsCalculatorService.circuitLAPTotal(this.circuits, simultaneityCoefficient));
  }

  simultaneityCoefficientDetermined() : number {
    let surfaces = this.surfacesService.getSurfaces(this.projectId);
    let totalSurface = this.projectsCalculatorService.totalSurface(surfaces);
    let apparentPower = this.projectsCalculatorService.apparentPower(surfaces);
    let electrificationDegree = this.projectsCalculatorService.electrificationDegree(totalSurface, apparentPower);
    return this.simultaneityCoefficient(electrificationDegree);
  }

  calculateCurrent(circuitType: CircuitType){
    const cosPhi = 0.9;
    const Volts = 220;
    if(circuitType == CircuitType.LAP){
      return parseFloat((this.circuitLAPTotal() / (Volts * cosPhi)).toFixed(2));
    }
    return parseFloat((this.powerLoad(circuitType) / (Volts * cosPhi)).toFixed(2));
  }

  nominalSection(circuit: Circuit) : number{
    let current = this.calculateCurrent(circuit.circuitType);
    let nominalSection = this.projectsCalculatorService.nominalSectionByCurrentIntensity(current);
    let nominalSections = this.projectsCalculatorService.sectionNominals();
    let minimalNominalSection = nominalSections.find(ns => ns.circuitType == circuit.circuitType);
    let tryLAP = circuit.circuitType == CircuitType.LAP;

    if(minimalNominalSection && nominalSection > minimalNominalSection.minSectionNominal) {
      return this.projectsCalculatorService.validateNominalSection(
        this.technicalFeatures(),
        circuit,
        nominalSection,
        this.powerLoad(circuit.circuitType),
        tryLAP);
    }
    return this.projectsCalculatorService.validateNominalSection(
      this.technicalFeatures(),
      circuit,
      minimalNominalSection.minSectionNominal,
      this.powerLoad(circuit.circuitType),
      tryLAP);
  }

  technicalFeatures(): TechnicalFeature[] {
    return this.projectsCalculatorService.tecnicalFeatures();
  }

  calculateTensionDrop(circuit: Circuit): number {
    let nominalSection = this.nominalSection(circuit);
    let powerLoad = this.powerLoad(circuit.circuitType);
    return this.projectsCalculatorService.calcuteTensionDrop(circuit, nominalSection, powerLoad);
  }

  tensionDrops() : TensionDrop[]{
    return this.projectsCalculatorService.tensionDrops();
  }

  tensionDrop(circuitType: CircuitType): number {
    let tensionDrops = this.projectsCalculatorService.tensionDrops();
    let tensionDrop = tensionDrops.find(td => td.circuitType == circuitType);
    return tensionDrop?.maxTensionDrop || 0;
  }

  isValidTension(circuit: Circuit): boolean {
    let tensionDropCalculated = this.calculateTensionDrop(circuit);
    let tendionDrop = this.tensionDrop(circuit.circuitType);
    return tensionDropCalculated <= tendionDrop;
  }

  sectionNominals() : SectionNominal[] {
    return this.projectsCalculatorService.sectionNominals();
  }

  calculateProtection(circuit: Circuit) : Protection {
    let current = this.calculateCurrent(circuit.circuitType);
    let isLAP = circuit.circuitType == CircuitType.LAP;
    let protectionCalculated = this.projectsCalculatorService.calculateProtection(current, isLAP);
    if(!this.isValidProtection(circuit, protectionCalculated)){
      let nominalSectionCalculated = this.nominalSection(circuit);
      let technicalFeature = this.projectsCalculatorService
        .tecnicalFeatures()
        .find( tf => tf.nominalSection == nominalSectionCalculated);

        if(Math.abs(technicalFeature.allowableCurrentIntensityInPipes - current) < 1){
        return this.projectsCalculatorService
          .calculateProtection(technicalFeature.allowableCurrentIntensityInPipes, isLAP);
      }
    }
    return protectionCalculated;
  }

  isValidProtection(circuit: Circuit, protection: Protection) : boolean {
    // 1.- Ib < I1
    // 2.- In <= Iz
    let nominalSectionCalculated = this.nominalSection(circuit);
    let Ib = this.calculateCurrent(circuit.circuitType);
    let Iz = this.projectsCalculatorService.tecnicalFeatures()
      .find( tf => tf.nominalSection == nominalSectionCalculated).allowableCurrentIntensityInPipes;
    let In = protection;
    let I1 = parseFloat((1.13 * In.protectionValue).toFixed(2));
    return Ib < I1 && In.protectionValue <= Iz;
  }

}
