import { Injectable } from '@angular/core';
import { Project } from '../models/project.model';

@Injectable({
  providedIn: 'root',
})
export class ProjectsService {
  private key = 'projects';

  saveProject(project: Project) {
    let projects = this.getProjects();
    project.id = projects.length + 1;
    projects = projects.filter(p => p.id != project.id);
    projects.push(project);
    localStorage.setItem(this.key, JSON.stringify(projects));
  }

  updateProject(id: number, updatedProject: Project) {
    let projects = this.getProjects();
    const index = projects.findIndex((project) => project.id == id);
    if (index > -1) {
      projects[index] = updatedProject;
      localStorage.setItem(this.key, JSON.stringify(projects));
    }
  }

  deleteProject(id: number) {
    let projects = this.getProjects();
    const projectsFiltered = projects.filter((project) => project.id != id);
    if(projectsFiltered){
      localStorage.setItem(this.key, JSON.stringify(projectsFiltered));
    }
  }

  getProject(): Project | null {
    const projects = this.getProjects();
    return projects.length > 0 ? projects[0] : null; // Return the first project for simplicity
  }

  getProjects(): Project[] {
    const projects = localStorage.getItem(this.key);
    return projects ? JSON.parse(projects) : [];
  }

  getProjectById(id: number): Project | undefined {
    const projects = localStorage.getItem(this.key);
    return projects ? JSON.parse(projects)?.find((project) => project.id == id) : undefined;
  }

}
