import { Injectable } from '@angular/core';
import { ProjectsService } from './projects.service';
import { Circuit } from '../models/circuit.model';

@Injectable({
  providedIn: 'root',
})
export class CircuitsService {

  constructor(private projectsService: ProjectsService) { }

  saveCircuit(projectId: number, circuit: Circuit) {
    let projects = this.projectsService.getProjects();
    let index = projects.findIndex(p => p.id == projectId);
    if(index != -1){
      let project = projects[index];

      if(!project.circuits)
        project.circuits = [];

      project.circuits.push(circuit);
      this.projectsService.updateProject(projectId, project);
    }
  }

  updateCircuit(projectid: number, updatedCircuit: Circuit){
    let projects = this.projectsService.getProjects();
    let index = projects.findIndex(p => p.id == projectid);
    if(index != -1){
      let circuits = projects[index]?.circuits;
      if(circuits){
        let indexCircuit = circuits.findIndex(s => s.name == updatedCircuit.name);
        if(indexCircuit != -1){
          circuits[indexCircuit] = updatedCircuit;
          projects[index].circuits = circuits;
          this.projectsService.updateProject(projectid, projects[index]);
        }
      }
    }
  }

  deleteCircuit(projectId: number, name: string) {
    let projects = this.projectsService.getProjects();
    let index = projects.findIndex(p => p.id == projectId);
    if(index != -1){
      let project = projects[index];
      project.circuits = project.circuits.filter(s => s.name != name);
      this.projectsService.updateProject(projectId, project);
    }
  }

  getCircuit(projectId: number, name: string): Circuit | null {
    const project = this.projectsService.getProjectById(projectId);
    return project?.circuits ? project?.circuits.find((circuit) => circuit.name == name) : null;
  }

  getCircuits(projectId: number): Circuit[] {
    const project = this.projectsService.getProjectById(projectId);
    return project?.circuits || [];
  }
}
