import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Surface } from '../models/surface.model';
import { SurfacesService } from 'app/pages/projects/services/surfaces.service';
import { ActivatedRoute } from '@angular/router';
import { SurfaceType } from '../models/surfacetype.model';
import { ProjectsCalculatorService } from '../services/projects-calculator.service';
import { ElectrificationDegreeType } from '../models/electrification-degree-type.model';
import { CircuitType } from '../models/circuittype.model';

@Component({
  selector: 'surfaces',
  templateUrl: './surfaces.component.html',
  styleUrls: ['./surfaces.component.scss'],
})
export class SurfacesComponent implements OnInit {
  projectId: number;
  surfaceForm: FormGroup;
  surfaces: Surface[];
  selectedSurface: Surface;
  isEdit: boolean;

  constructor(
    private surfacesService: SurfacesService,
    private fb: FormBuilder,
    private activatedRoute: ActivatedRoute,
    private projectCalcultatorService: ProjectsCalculatorService,
  ) { }

  ngOnInit(): void {
    this.surfaceForm = this.fb.group({
      type: ['', Validators.required],
      environment: ['', Validators.required],
      width: ['', Validators.required],
      length: ['', Validators.required],
    });

    this.projectId = parseInt(this.activatedRoute.snapshot.params['projectId']);
    this.surfaces = this.surfacesService.getSurfaces(parseInt(this.activatedRoute.snapshot.params['projectId']));
    this.isEdit = false;
  }

  saveSurface() {
    if(!this.surfaceForm.invalid){
      if(!this.isEdit){
        let newSurface = this.surfaceForm.value as Surface;
        newSurface.artifacts = [];
        this.surfacesService.saveSurface(this.projectId, newSurface);
      } else {
        let surfaceToUpdate = this.surfaceForm.value as Surface;
        this.selectedSurface.type = surfaceToUpdate.type;
        this.selectedSurface.environment = surfaceToUpdate.environment;
        this.selectedSurface.width = surfaceToUpdate.width;
        this.selectedSurface.length = surfaceToUpdate.length;
        this.surfacesService.updateSurface(this.projectId, this.selectedSurface);
      }
      this.cleanForm();
    } else {
      alert('the form is invalid');
    }
  }

  cleanForm(): void {
    this.isEdit = false;
    this.surfaceForm.controls['type'].setValue('');
    this.surfaceForm.controls['environment'].setValue('');
    this.surfaceForm.controls['width'].setValue('');
    this.surfaceForm.controls['length'].setValue('');
    this.surfaces = this.surfacesService.getSurfaces(this.projectId);
  }

  editSurface(environment: string) {
    this.selectedSurface = this.surfacesService.getSurface(this.projectId, environment);
    this.surfaceForm.patchValue(this.selectedSurface);
    this.isEdit = true;
  }

  deleteSurface(environment: string){
    this.surfacesService.deleteSurface(this.projectId, environment);
    this.cleanForm();
  }

  isFormInvalid() : boolean {
    return this.surfaceForm.controls['type'].hasError('required')
    || this.surfaceForm.controls['environment'].hasError('required')
    || this.surfaceForm.controls['width'].hasError('required')
    || this.surfaceForm.controls['length'].hasError('required');
  }

  coveredSurfaces() : Surface[]{
    let surfaces = this.surfacesService.getSurfaces(this.projectId);
    return surfaces.filter(surface => surface.type == SurfaceType.Covered);
  }

  semiCoveredSurfaces() : Surface[]{
    let surfaces = this.surfacesService.getSurfaces(this.projectId);
    return surfaces.filter(surface => surface.type == SurfaceType.SemiCovered);
  }

  totalSurfaceCovered() : number {
    let surfaces = this.surfacesService.getSurfaces(this.projectId);
    return this.projectCalcultatorService.totalSurfaceCovered(surfaces);
  }

  totalSurfaceSemiCovered() : number {
    let surfaces = this.surfacesService.getSurfaces(this.projectId);
    return this.projectCalcultatorService.totalSurfaceSemiCovered(surfaces);
  }

  totalSurface() : number {
    let surfaces = this.surfacesService.getSurfaces(this.projectId);
    return this.projectCalcultatorService.totalSurface(surfaces);
  }

  calculateMaximumConsumptionDetermined(): number{
    let surfaces = this.surfacesService.getSurfaces(this.projectId);
    return this.projectCalcultatorService.calculateMaximumConsumptionDetermined(surfaces);
  }

  apparentPower() : number {
    let surfaces = this.surfacesService.getSurfaces(this.projectId);
    return this.projectCalcultatorService.apparentPower(surfaces);
  }

  electrificationDegree() : ElectrificationDegreeType {
    return this.projectCalcultatorService.electrificationDegree(this.totalSurface(), this.apparentPower());
  }

  isMinimalElectrificationDegree() : boolean {
    return this.electrificationDegree() == ElectrificationDegreeType.Minimal;
  }

  isMediumElectrificationDegree() : boolean {
    return this.electrificationDegree() == ElectrificationDegreeType.Medium;
  }

  isHighElectrificationDegree() : boolean {
    return this.electrificationDegree() == ElectrificationDegreeType.High;
  }

  isUpperElectrificationDegree() : boolean {
    return this.electrificationDegree() == ElectrificationDegreeType.Upper;
  }

  circuits() : CircuitType[]{
    let electrificationDegree = this.electrificationDegree();
    return this.projectCalcultatorService.circuitsByelectrificationDegree(electrificationDegree);
  }

}
