import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Project } from '../models/project.model';
import { ProjectsService } from 'app/pages/projects/services/projects.service';

@Component({
  selector: 'projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss'],
})
export class ProjectsComponent implements OnInit {

  projectForm: FormGroup;
  projects: Project[];
  selectedProject: Project;
  isEdit: boolean;

  constructor(
    private projectsService: ProjectsService,
    private fb: FormBuilder) { }

  ngOnInit(): void {
    this.projectForm = this.fb.group({
      name: ['', Validators.required],
    });
    this.projects = this.projectsService.getProjects();
    this.isEdit = false;
  }

  saveProject() {
    if(!this.projectForm.invalid){
      if(!this.isEdit){
        let newProject = this.projectForm.value as Project;
        newProject.surfaces = [];
        newProject.circuits = [];
        this.projectsService.saveProject(newProject);
      } else {
        let projectToUpdate = this.projectForm.value as Project;
        if(!this.selectedProject?.surfaces){
          this.selectedProject.surfaces = [];
        }
        if(!this.selectedProject?.circuits){
          this.selectedProject.circuits = [];
        }
        this.selectedProject.name = projectToUpdate.name;
        this.projectsService.updateProject(this.selectedProject.id, this.selectedProject);
      }
      this.cleanForm();
    } else {
      alert('the form is invalid');
    }
  }
  cleanForm(): void {
    this.isEdit = false;
    this.projectForm.controls['name'].setValue('');
    this.projects = this.projectsService.getProjects();
  }

  editProject(id: number) {
    this.selectedProject = this.projectsService.getProjectById(id);
    this.projectForm.patchValue(this.selectedProject);
    this.isEdit = true;
  }

  deleteProject(id: number){
    this.projectsService.deleteProject(id);
    this.cleanForm();
  }
}
