import { Injectable } from '@angular/core';
import { ProjectsService } from './projects.service';
import { Surface } from '../models/surface.model';

@Injectable({
  providedIn: 'root',
})
export class SurfacesService {

  constructor(
    private projectsService: ProjectsService,
  ) { }

  saveSurface(projectId: number, surface: Surface) {
    let projects = this.projectsService.getProjects();
    let index = projects.findIndex(p => p.id == projectId);
    if(index != -1){
      let project = projects[index];

      if(!project.surfaces)
        project.surfaces = [];

      project.surfaces.push(surface);
      this.projectsService.updateProject(projectId, project);
    }
  }

  updateSurface(projectid: number, updatedSurface: Surface){
    let projects = this.projectsService.getProjects();
    let index = projects.findIndex(p => p.id == projectid);
    if(index != -1){
      let surfaces = projects[index]?.surfaces;
      if(surfaces){
        let indexSurface = surfaces.findIndex(s => s.environment == updatedSurface.environment);
        if(indexSurface != -1){
          surfaces[indexSurface] = updatedSurface;
          projects[index].surfaces = surfaces;
          this.projectsService.updateProject(projectid, projects[index]);
        }
      }
    }
  }

  deleteSurface(projectId: number, environment: string) {
    let projects = this.projectsService.getProjects();
    let index = projects.findIndex(p => p.id == projectId);
    if(index != -1){
      let project = projects[index];
      project.surfaces = project.surfaces.filter(s => s.environment != environment);
      this.projectsService.updateProject(projectId, project);
    }
  }

  getSurface(projectId: number, environment: string): Surface | null {
    const project = this.projectsService.getProjectById(projectId);
    return project?.surfaces ? project?.surfaces.find((surface) => surface.environment == environment) : null;
  }

  getSurfaces(projectId: number): Surface[] {
    const project = this.projectsService.getProjectById(projectId);
    return project?.surfaces || [];
  }


}
