import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Artifact } from '../models/artifact.model';
import { ArtifactsService } from 'app/pages/projects/services/artifacts.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'artifacts',
  templateUrl: './artifacts.component.html',
  styleUrls: ['./artifacts.component.scss'],
})
export class ArtifactsComponent implements OnInit {
  projectId: number;
  environment: string;
  artifactForm: FormGroup;
  artifacts: Artifact[];
  selectedArtifact: Artifact;
  isEdit: boolean;

  constructor(
    private artifactsService: ArtifactsService,
    private fb: FormBuilder,
    private activatedRoute: ActivatedRoute) { }

  ngOnInit(): void {
    this.artifactForm = this.fb.group({
      name: ['', Validators.required],
      powerConsumption: ['', Validators.required],
      summerDay: [false, Validators.required],
      summerNight: [false, Validators.required],
      winterDay: [false, Validators.required],
      winterNight: [false, Validators.required],
    });

    this.projectId = this.activatedRoute.snapshot.params['projectId'];
    this.environment = this.activatedRoute.snapshot.params['environment'];
    this.artifacts = this.artifactsService.getArtifacts(
      this.activatedRoute.snapshot.params['projectId'],
      this.activatedRoute.snapshot.params['environment']);
    this.isEdit = false;
  }

  saveArtifact() {
    if(!this.artifactForm.invalid){
      if(!this.isEdit){
        let newArtifact = this.artifactForm.value as Artifact;
        this.artifactsService.saveArtifact(this.projectId, this.environment, newArtifact);
      } else {
        let surfaceToUpdate = this.artifactForm.value as Artifact;
        this.selectedArtifact.name = surfaceToUpdate.name;
        this.selectedArtifact.powerConsumption = surfaceToUpdate.powerConsumption;
        this.selectedArtifact.summerDay = surfaceToUpdate.summerDay;
        this.selectedArtifact.summerNight = surfaceToUpdate.summerNight;
        this.selectedArtifact.winterDay = surfaceToUpdate.winterDay;
        this.selectedArtifact.winterNight = surfaceToUpdate.winterNight;
        this.artifactsService.updateArtifacts(this.projectId, this.environment, this.selectedArtifact);
      }
      this.cleanForm();
    } else {
      alert('the form is invalid');
    }
  }

  cleanForm(): void {
    this.isEdit = false;
    this.artifactForm.controls['name'].setValue('');
    this.artifactForm.controls['powerConsumption'].setValue('');
    this.artifactForm.controls['summerDay'].setValue(false);
    this.artifactForm.controls['summerNight'].setValue(false);
    this.artifactForm.controls['winterDay'].setValue(false);
    this.artifactForm.controls['winterNight'].setValue(false);
    this.artifacts = this.artifactsService.getArtifacts(this.projectId, this.environment);
  }

  editArtifact(environment: string, name: string) {
    this.selectedArtifact = this.artifactsService.getArtifact(this.projectId, environment, name);
    this.artifactForm.patchValue(this.selectedArtifact);
    this.isEdit = true;
  }

  deleteArtifact(environment: string, name: string){
    this.artifactsService.deleteArtifact(this.projectId, environment, name);
    this.cleanForm();
  }

  isFormInvalid() : boolean {
    return this.artifactForm.controls['name'].hasError('required')
    || this.artifactForm.controls['powerConsumption'].hasError('required')
    || this.artifactForm.controls['summerDay'].hasError('required')
    || this.artifactForm.controls['summerNight'].hasError('required')
    || this.artifactForm.controls['winterDay'].hasError('required')
    || this.artifactForm.controls['winterNight'].hasError('required');
  }
}
